// jQuery Steps

.wizard {
  > .content {
    min-height: 20rem;
    overflow: auto;
    // background: rgb(248, 248, 248);
    background: $white;
    border: 1px solid $border-color;
  }
  > .steps {
    ul {
      a {
        &, &:hover {
          padding: .4rem 1rem;
        }
        border-radius: 3px;
        .number {
          font-size: .875rem;
        }
      }
      .current {
        a {
          background: theme-color(primary);
          &:hover {
            background: darken(theme-color(primary), 5%);
          }
        }
      }
      .done {
        a {
          background: lighten(theme-color(primary), 30%);
          &:hover {
            background: lighten(theme-color(primary), 25%);
          }
        }
      }
    }
  }
  > .actions {
    margin-top: 12px;
    a {
      &, &:hover {
        @extend .btn;
        @extend .btn-primary;
      }
    }
    .disabled {
      a {
        @extend .btn;
        @extend .btn-outline-primary;
      }
    }
  }

  &.vertical {
    > .actions {
      margin-top: 8px;
    }
  }
}