/* Dropdowns */

.dropdown,
.btn-group {
  .dropdown-toggle {
    &:after {
      border-top: 0;
      border-right: 0;
      border-left: 0;
      border-bottom: 0;
      font: normal normal normal 24px/1 "feather";
      content: "\e842";  
      width: auto;
      height: auto;
      vertical-align: middle;
      line-height: .625rem;
      font-size: .875rem;
      margin-left: .3rem;
    }
  }
  .dropdown-menu {
    padding: .35rem;
    margin-top: 0;
    box-shadow: 0 0 10px 0 rgba(183,192,206,.2); 
    border-radius: 0;   
    .dropdown-item {
      font-size: .812rem;
      padding: .25rem .875rem;
      transition: all .2s ease-in-out;
      border-radius: 2px;
      i, svg {
        color: $text-muted;
        transition: all .2s ease-in-out;
      }
      &:hover {
        background-color: lighten(theme-color(primary), 34%);
        &, i, svg {
          color: theme-color(primary);
        }        
      }
      &:active {
        background: initial;
      }
    }
  }
}
